<template>
        <router-link :to="{path: model.url, query: { menuCode: model.menuCode}}" v-if="model.arr.length == 0" :key="model.menuCode">
            <el-menu-item :index="model.menuCode" style="text-align: left;">
                <i :class="model.iName" style="color:white"></i>
                <span slot="title">{{model.name}}</span>
            </el-menu-item>
        </router-link>
        <el-submenu :index="model.menuCode" :key="model.menuCode" style="text-align: left;" v-else>
              <template slot="title">
                <i :class="model.iName" style="color:white"></i>
                <span slot="title" style="color:RGB(124,176,253);">{{model.name}}</span>
              </template>
              <menus v-for="(model2, index) in model.arr" :model="model2" :key="index"></menus>
        </el-submenu>
</template>
<script>
export default {
  name: 'menus',
  props: ['model'],
  data() {
    return {
    };
  },
  methods: {
  }
};
</script>
<style scoped>
.mainViewCell-leftBox {
  height: 100%;
  overflow: hidden;
  overflow-y: scroll;
  width: 300px;
  animation: 1s;
}
.mainViewCell-leftBox::-webkit-scrollbar {
  display: none;
}
.mainViewCell-leftBox {
  scrollbar-width: none;
}
.mainViewCell-leftBox {
  /*隐藏滚动条，当IE下溢出，仍然可以滚动*/
  -ms-overflow-style: none;
  /*火狐下隐藏滚动条*/
  overflow: -moz-scrollbars-none;
}
.mainViewCell-leftBox .MenuBtn {
  position: absolute;
  right: 0;
}
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 250px;

  background-color: RGB(28, 99, 200);
}
.el-menu {
  border-right: none;
}
.el-menu-item,
.el-submenu {
  background-color: RGB(28, 99, 200);
}
.el-menu-item,
.el-submenu__title {
  color: RGB(124, 176, 253);
}

.el-menu-item.is-active {
  color: RGB(234, 251, 6);
}
/* ========== */
.el-menu-item:hover,
.el-menu-item:focus {
  background-color: #1c63c8 !important;
}
/* =========== */
</style>


