<template>
  <div class="process-base">
    <div class="mainViewCell">
      <div
        :class="isCollapse?'MenuBtn transform-arrow':'MenuBtn'"
        :style="isCollapse?'left:61px;':'left:234px;'"
        @click="MenuBtnClick"
      ></div>
      <div class="mainViewCell-leftBox" :style="isCollapse?'width:64px;':''">
        <!-- ------------------------------- -->
        <el-menu
          :default-active="firstPage"
          class="el-menu-vertical-demo"
          @open="handleOpen"
          @close="handleClose"
          @select="selectFun"
          :collapse="isCollapse"
          :unique-opened="true"
          style="background-color:RGB(24,79,168)"
        >
          <menus v-for="(model, index) in MenuArr" :model="model" :key="index"></menus>
        </el-menu>
        <!-- ---------------------------------->
      </div>
      <!-- =========================第二视口 -->
      <div class="mainViewCell-rightBox">
        <router-view name="SecondView"></router-view>
      </div>
      <!-- ========================= 第二视口-->
    </div>
  </div>
</template>
<script>
import menus from "@/components/commonComp/menus";
export default {
  components: {menus},
  created() {},
  watch: {
    "$store.state.leftMenuBox": {
      handler(newVal) {
        this.firstPage = newVal;
        this.storage.set("leftMenuBox", newVal);
      }
    },
    "$store.state.leftMenuBoxBody": {
      handler(newVal) {
        this.MenuArr = newVal;
      },
      deep: true
    }
  },
  data() {
    return {
      firstPage: this.$store.state.leftMenuBox,
      isCollapse: false,
      MenuArr: this.$store.state.leftMenuBoxBody
    };
  },
  methods: {
    handleOpen(key, keyPath) {},
    handleClose(key, keyPath) {},
    selectFun(a) {
      this.$store.commit("leftMenuBox", {
        leftMenuBox: a
      });
    },
    MenuBtnClick() {
      this.isCollapse = !this.isCollapse;
    }
  }
};
</script>
<style scoped>
.mainViewCell-leftBox {
  height: 100%;
  overflow: hidden;
  overflow-y: scroll;
  width: 300px;
  animation: 1s;
}
.mainViewCell-leftBox::-webkit-scrollbar {
  display: none;
}
.mainViewCell-leftBox {
  scrollbar-width: none;
}
.mainViewCell-leftBox {
  /*隐藏滚动条，当IE下溢出，仍然可以滚动*/
  -ms-overflow-style: none;
  /*火狐下隐藏滚动条*/
  overflow: -moz-scrollbars-none;
}
.mainViewCell-leftBox .MenuBtn {
  position: absolute;
  right: 0;
}
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 250px;

  background-color: RGB(28, 99, 200);
}
.el-menu {
  border-right: none;
}
.el-menu-item,
.el-submenu {
  background-color: RGB(28, 99, 200);
}
.el-menu-item,
.el-submenu__title {
  color: RGB(124, 176, 253);
}

.el-menu-item.is-active {
  color: RGB(234, 251, 6);
}
/* ========== */
.el-menu-item:hover,
.el-menu-item:focus {
  background-color: #1c63c8 !important;
}
/* =========== */
</style>


